/* activity8.css */
@import url("https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap");

.number-line {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background-color: var(--text-color);
  border-radius: 10px;
  height: 3px;
  left: 5%;
  position: relative;
  top: 5vh;
  width: 90%;
  transform: translateY(-50%);
}

.number-dot {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: var(--text-color);
  font-size: 25px;
  font-family: var(--font-family);
  position: relative;
  padding: 2%;
}

.number-dot::before {
  content: "";
  width: 0.7vw;
  height: 0.7vw;
  background-color: var(--text-color);
  border-radius: 50%;
  position: absolute;
  top: 3%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.blue-dot::before {
  background-color: blue;
}

.blue-dot {
  color: blue;
}

.red-dot::before {
  background-color: red;
}

.red-dot {
  color: red;
}

.car_img {
  position: absolute;
  top: -2vw;
  left: 50%;
  transform: translateX(-50%);
  width: 4vw;
  height: auto;
}

.rectangle-container {
  display: flex;
  justify-content: space-around;
  margin-top: 17vh;
}

.rectangle2 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: var(--font-family);
}

.square {
  width: 6vw;
  height: 6vw;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: var(--secondary-color);
  border: 1.5px solid var(--text-color);
  cursor: pointer;
  font-size: 2.5vw;
  font-family: var(--font-family);
  font-weight: bold;
}

.red-text {
  color: red;
}

.blue-text {
  color: blue;
}

.black-text {
  color: var(--text-color);
}

.grid {
  width: 60%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 3vh;
  margin-left: 1vh;
}

@media (max-width: 1124px) {
  .grid {
    width: 50%;
  }
}

.rectangleHard {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: var(--font-family);
}

.squareHard {
  width: 5vw;
  height: 5vw;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: var(--secondary-color);
  border: 1.5px solid var(--text-color);
  cursor: pointer;
  font-size: 2vw;
  font-family: var(--font-family);
  font-weight: bold;
  color: black;
}

@media (max-width: 1124px) {
  .squareHard {
    width: 7vw;
    height: 7vw;
  }
}

.rectangle-container-hard {
  display: flex;
  justify-content: flex-start;
  margin-top: 5vh;
  gap: 5vh;
  margin-left: 1vw;
}

/* Triangle styles */
.triangle {
  position: absolute;
  border-style: solid;
}

.triangle.up {
  bottom: 5vw;
  left: -0.2vh;
  transform: translateX(-50%);
  border-width: 0 0.7vw 0.7vw 0.7vw;
  border-color: transparent transparent currentColor transparent;
}

.triangle.down {
  top: 2.5vw;
  left: -0.2vh;
  transform: translateX(-50%);
  border-width: 0.7vw 0.7vw 0 0.7vw;
  border-color: currentColor transparent transparent transparent;
}

.triangle.left {
  right: 5vw;
  top: -0.2vh;
  transform: translateY(-50%);
  border-width: 0.7vw 0.7vw 0.7vw 0;
  border-color: transparent currentColor transparent transparent;
}

.triangle.right {
  left: 2.5vw;
  top: -0.2vh;
  transform: translateY(-50%);
  border-width: 0.7vw 0 0.7vw 0.7vw;
  border-color: transparent transparent transparent currentColor;
}

/* Plus and Minus sign styles */
.plus-sign {
  position: absolute;
  font-size: 2vw;
  font-weight: bold;
  color: var(--text-color);
}

.plus-sign.up {
  top: -4.2vw;
  left: -0.2vh;
  transform: translate(-50%, -50%);
}

.plus-sign.down {
  top: 3.7vw;
  left: -0.2vh;
  transform: translate(-50%, -50%);
}

.plus-sign.left {
  right: 5.5vw;
  top: -0.7vh;
  transform: translate(-50%, -50%);
}

.plus-sign.right {
  left: 4vw;
  top: -0.7vh;
  transform: translate(-50%, -50%);
}
