/* background.css */
.background {
  background-color: #272f70;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.background .div {
  background-color: #272f70;
  height: 100vh;
  overflow: hidden;
  position: relative;
  width: 100vw;
}

.background .blue-ellipse {
  background-color: #0f175f;
  filter: blur(var(--ellipse-blur, 20px));
  height: var(--ellipse-height, 200px);
  width: var(--ellipse-width, 200px);
  position: absolute;
  top: var(--ellipse-top, 50%);
  left: var(--ellipse-left, 50%);
  transform: translate(-50%, -50%);
}

.background .white-ellipse {
  background-color: #eceefa;
  border-radius: var(--ellipse-radius, 50%);
  filter: blur(var(--ellipse-blur, 20px));
  height: var(--ellipse-height, 200px);
  width: var(--ellipse-width, 200px);
  position: absolute;
  top: var(--ellipse-top, 50%);
  left: var(--ellipse-left, 50%);
  transform: translate(-50%, -50%);
}
