/* activity1.css */
@import url("https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap");

.star-A1 {
  width: 2.2vw;
  aspect-ratio: 1/1;
  cursor: pointer;
}

.cloud-A1 {
  position: absolute;
  height: 44vh;
  width: 27vw;
  margin-top: 1vh;
}

@media (max-width: 1124px) {
  .cloud-A1 {
    width: 30vw;
  }
}

.div-A1 {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info-input-A1 {
  width: 4vw;
  height: 4vw;
  text-align: center;
  border-radius: 5%;
  background-color: var(--secondary-color);
  border: 2px solid var(--text-color);
  font-size: 4vw;
  font-family: var(--font-family);
  color: var(--text-color);
  margin-top: 32vh;
}

.button-container-A1 {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 1vh;
}

.operator-button-A1 {
  width: 4vw;
  height: 4vw;
  background-color: var(--primary-color);
  border: 1px solid var(--text-color);
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5vw;
  font-family: var(--font-family);
  color: var(--text-color);
  cursor: pointer;
  outline: none;
  margin-left: 1vh;
  margin-right: 1vh;
  z-index: 2;
}

.label-container-A1 {
  display: flex;
  justify-content: center;
}

.operator-label-A1 {
  margin: 0 0.7vw;
  font-size: 1.5vw;
  font-family: var(--font-family);
  color: var(--text-color);
}
